export default {
  async asyncData ({ $axios }) {
    try {
      let response = await $axios.$get('https://ecommstore2019.herokuapp.com/api/orders')

      if (response.success) {
        return {
          orders: response.products
        }
      }
    } catch (error) {
      console.log(error);
    }
  }
}
